.social-container{
    display: flex;
    flex-direction: row;
    justify-content:right;
}

.social-container a{
    margin: 0px 10px 0px 10px;
    padding: 2px;
}

.social-container a img{
    width: 48px;
}

.social-container a:hover{
    padding: 0px;
}

.social-container a:hover img{
    width: 52px;
}

